import React from 'react'

export default function TeamItem(props) {
  return (
    <div>
            <div className="member">
              <img src={props.image} className="img-fluid round-border" alt=""/>
              <div className="member-info">
                <div className="member-info-content ">
                  <h4>{props.name}</h4>
                  <span>{props.profession}</span>
                  <div className="social">
                    <a href={props.twitter}><i className="bi bi-twitter"></i></a>
                    <a href={props.github}><i className="bi bi-github"></i></a>
                    <a href={props.linkedin}><i className="bi bi-linkedin"></i></a>
                  </div>
                  </div>
                </div>
              </div>          
    </div>
  )
}
