import React, { useEffect } from "react";
import GLightbox from 'glightbox';
import 'glightbox/dist/css/glightbox.css';
import AOS from "aos";
import "aos/dist/aos.css";
import Swiper from 'swiper';
import 'swiper/swiper-bundle.css';
import Isotope from 'isotope-layout';

import image1 from "../images/AwsLogo3.jpg";

export default function Navbar() {
  useEffect(() => {
    // Code for initializing AOS, GLightbox, Swiper, Isotope, and other functionalities
    AOS.init({
      duration: 1000,
      easing: 'ease-in-out',
      once: true,
      mirror: false
    });

    GLightbox({
      selector: '.portfolio-lightbox'
    });

    new Swiper('.portfolio-details-slider', {
      speed: 400,
      loop: true,
      autoplay: {
        delay: 5000,
        disableOnInteraction: false
      },
      pagination: {
        el: '.swiper-pagination',
        type: 'bullets',
        clickable: true
      }
    });

    new Swiper('.testimonials-slider', {
      speed: 600,
      loop: true,
      autoplay: {
        delay: 5000,
        disableOnInteraction: false
      },
      slidesPerView: 'auto',
      pagination: {
        el: '.swiper-pagination',
        type: 'bullets',
        clickable: true
      }
    });

    let portfolioContainer = document.querySelector('.portfolio-container');
    if (portfolioContainer) {
      let portfolioIsotope = new Isotope(portfolioContainer, {
        itemSelector: '.portfolio-item'
      });

      let portfolioFilters = document.querySelectorAll('#portfolio-flters li');

      portfolioFilters.forEach(filter => {
        filter.addEventListener('click', function (e) {
          e.preventDefault();

          portfolioFilters.forEach(el => {
            el.classList.remove('filter-active');
          });

          this.classList.add('filter-active');

          portfolioIsotope.arrange({
            filter: this.getAttribute('data-filter')
          });

          portfolioIsotope.on('arrangeComplete', function () {
            AOS.refresh();
          });
        });
      });
    }
  }, []);

  useEffect(() => {
    const navbarlinksActive = () => {
      let position = window.scrollY + 200;
      let navbarlinks = document.querySelectorAll('#navbar .scrollto');

      navbarlinks.forEach(navbarlink => {
        if (!navbarlink.hash) return;

        let section = document.querySelector(navbarlink.hash);
        if (!section) return;

        if (
          position >= section.offsetTop &&
          position <= section.offsetTop + section.offsetHeight
        ) {
          navbarlink.classList.add('active');
        } else {
          navbarlink.classList.remove('active');
        }
      });
    };

    navbarlinksActive();

    window.addEventListener('scroll', navbarlinksActive);
  }, []);

  const toggleMobileMenu = () => {
    let navbar = document.getElementById('navbar');
    let navbarToggle = document.querySelector('.mobile-nav-toggle');

    navbar.classList.toggle('navbar-mobile');
    navbarToggle.classList.toggle('bi-list');
    navbarToggle.classList.toggle('bi-x');
  };

  const handleMobileMenuItemClick = () => {
    let navbar = document.getElementById('navbar');
    let navbarToggle = document.querySelector('.mobile-nav-toggle');

    if (navbar.classList.contains('navbar-mobile')) {
      navbar.classList.remove('navbar-mobile');
      navbarToggle.classList.toggle('bi-list');
      navbarToggle.classList.toggle('bi-x');
    }
  };

  return (
    <div>
      <header id="header" className="fixed-top d-flex align-items-center">
        <div className="container d-flex justify-content-between">
          <div>
            <a href="index.html" id="logo">
              <img
                src={image1}
                alt="logo"
                style={{ height: "65px", width: "65px" }}
              />
            </a>
          </div>
          <nav id="navbar" className="navbar">
            <ul>
              <li>
                <a className="nav-link scrollto active" href="#hero" onClick={handleMobileMenuItemClick}>
                  Home
                </a>
              </li>
              <li>
                <a className="nav-link scrollto" href="#about" onClick={handleMobileMenuItemClick}>
                  About
                </a>
              </li>
              <li>
                <a className="nav-link scrollto" href="#gallery" onClick={handleMobileMenuItemClick}>
                  Gallery
                </a>
              </li>
              
              <li>
                <a className="nav-link scrollto" href="#events" onClick={handleMobileMenuItemClick}>
                  Events
                </a>
              </li>
              <li>
                <a className="nav-link scrollto" href="#team" onClick={handleMobileMenuItemClick}>
                  Team
                </a>
              </li>
              <li>
                <a className="nav-link scrollto" href="#faqs" onClick={handleMobileMenuItemClick}>
                  FAQs
                </a>
              </li>
              <li>
                <a className="nav-link scrollto" href="#footer" onClick={handleMobileMenuItemClick}>
                  Contact Us
                </a>
              </li>
              {/*<li>
                <a className="nav-link scrollto" href="#contact" onClick={handleMobileMenuItemClick}>
                  Contact
                </a>
              </li>*/}
            </ul>
            <i className="bi bi-list mobile-nav-toggle" onClick={toggleMobileMenu}></i>
          </nav>
        </div>
      </header>
    </div>
  );
}
