import React from "react";
import slider from "../images/slider.png";
import Benefit from "./Benefit";


export default function About() {
  
  return (
    <div>
            <section
        id="hero"
        className="clearfix"
      >
        <div className="container" data-aos="fade-up">
          <div className="hero-img" data-aos="zoom-out" data-aos-delay="200">
            <img src={slider} alt="" className="img-fluid" />
          </div>

          <div className="hero-info " data-aos="zoom-in" data-aos-delay="100">
            <h2>
              AWS Cloud Club IIPS
              <br />
              <span >Upskill and Upgrade</span>
              <br />
              your AWS knowledge
              <br/> with us !
            </h2>
            
            <p >"Unmatched, we stand as the only AWS Cloud Club in Madhya Pradesh !"</p>
            <div className="btn-adjust">
              <a href="https://www.meetup.com/aws-cloud-club-iips/" class="btn home-btn-common home_btn" type="button" target="_blank">
                Join US
              </a>

              <a style={{ marginLeft: "20px" }} href="https://community.aws/students" class="btn home-btn-common home_btn" type="button" target="_blank">
                Learn More
              </a>
            </div>

          </div>
        </div>

      </section>

      
      <section id="about">
       <Benefit/>
      </section>
    </div>
  );
}
