import React from 'react'

export default function EventItem(props) {
  return (
    <div>
       
            <img class="card-img-top" src={props.image} alt="Core image" />
            <div class="card-body">


              <h5 class="title text-center">{props.event_name}</h5>
              <a href={props.link} class="home-btn btn-common opportunity_btn" type="button" target="_blank">


                {props.button_name}
              </a>
              <br /> <br />

            </div>
          
          
          
    </div>
  )
}