import React, { useState } from "react";
const Drop = require("../images/arrow.png")
const faq = require("../images/FAQ.png")
const faqData = [
  {
    question: "What are Aws Cloud Clubs?",
    answer:
      "AWS Cloud Clubs are a type of user group by students, for students. They are led by student leaders called Captains who organize events on their campus to earn perks such as certification exam vouchers, resume reviews, participation in cross-club events, bespoke learning initiatives, and more. The program is designed predominantly for upskilling in AWS technology but also includes a social aspect. The Captain program is very selective with a -5% acceptance rate for new Captains.",
  },
  {
    question: "Do Cloud Club members need to have experience with AWS?",
    answer:
      "All students and learners are welcome to join Cloud Clubs as a member. Captains, however, need to be curious and, preferably, versed in AWS technology to lead a Club. Tip: get an AWS certification",
  },
  {
    question: "Why should I join AWS Cloud Club?",
    answer:
      "Gain valuable experience engaging with partners, AWS employees, and peers across universities in your region and worldwide. And yes, some cool swags too!",
  },
  {
    question: "How AWS Cloud Club help me in my career or studies?",
    answer:
      "AWS Cloud Club can help you in your career and studies by providing opportunities to learn new skills from AWS professionals, build a network, gain hands-on experience, build your resume, and prepare for AWS certification exams.",
  },
  {
    question: "What skills do you learn in AWS Cloud Club?",
    answer:
      "The cloud computing market is projected to reach a staggering $815 billion by 2025, with AWS holding the largest market share. Expect to build cloud skills like: Deploying apps & services, Understand key security principles and best practices, Automating cloud resources, Collaborating with cloud tools like GitHub, CloudFormation, and other collaboration platforms. Find mentors, share knowledge, and boost your cloud network.",
  },
];

export default function FAQ() {
  const [openIndex, setOpenIndex] = useState(null);

  const toggleAnswer = (index) => {
    setOpenIndex((prevIndex) => (prevIndex === index ? null : index));
  };

  return (
    <div id="faqs" className="container-faq">
      <article>
        <img className="img-container" src={faq}  alt=""/>
        <div className="faq">
          {/* <h2>FAQs</h2> */}
          {faqData.map((item, index) => (
            <div key={index} className="q-a">
              <div
                className="q-wrapper"
                onClick={() => toggleAnswer(index)}
                style={{ cursor: "pointer" }}
              >
                <h5 tabIndex="0">{item.question}</h5>
                <img src={Drop} alt="hello" style={{width : "15px"}}/>
              </div>
              {(openIndex === index && <p>{item.answer}</p>)}
            </div>
          ))}
        </div>
      </article>
    </div>
  );
}
